
import { defineComponent } from 'vue'
type ObjectData = {
  id: number,
  name: string,
  [word: string]: string | number | boolean
}

export default defineComponent({
  name: 'ComparsionItems',
  props: {
    content: String
  },
  data () {
    return {
      comparsion: this.content,
      checkout: false
    }
  },
  watch: {
    checkout (newValue) {
      this.$emit('checkout', newValue)
    }
  }
})
